import React from 'react';
import classes from './Appointment.module.css';
import AppointmentIcon from '../../Images/calendar-clock.png';
import ScheduledIcon from '../../Images/scheduled-check.png';
import CustomDatePicker from '../DatePicker/DatePicker';
import axios from 'axios';
import ScheduledAppointments from '../scheduledAppointments/ScheduleAppointments'
import WarningIcon from '../../Images/warning_icon.png'
import { grey } from '@material-ui/core/colors';


const url = process.env.REACT_APP_URL;


class Appointment extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedDoctor: "Select Doctor",
            selectedDepartment: "0",
            departmentData: [],
            doctorData: [],
            selectedDepartmentData: "",
            patientName: "",
            selectedTime: "",
            selectedDate: "",
            selectedDepartmentUUID: "",
            mandatoryValidation: false,
            startTimeForEdit: null,
            editStatus: false,
            editAppointmentUUID: null
        };
    }
    auth = {
        username: 'superman',
        password: 'Admin123'
    };
    selectedDate = ''
    componentDidMount() {
        axios.get(`${url}/appointmentService/all/full`, {
            auth: this.auth
        })
            .then(responce => {
                this.setState({ departmentData: responce.data })
            })
            .catch(error => {
                console.log("Error", error)
            })

        axios.get(`${url}/provider`, {
            auth: this.auth,
            params: { v: "custom:(display,person,uuid,retired,attributes:(attributeType:(display),value,voided))" }
        })
            .then(responce => {
                this.setState({ doctorData: responce.data.results })
            })
            .catch(error => {
                console.log("Error", error)
            })
    }

    render() {
        const handleDate = (date) => {
            this.selectedDate = date;
        }

        const getServiceavAilability = (department) => {
            if (department !== "") {
                const departmentuuid = getDepaUUID(department);
                this.setState({ selectedDepartmentUUID: departmentuuid });
                axios.get(`${url}/appointmentService`, {
                    auth: this.auth,
                    params: { uuid: departmentuuid }
                })
                    .then(responce => {
                        this.setState({ selectedDepartmentData: responce.data })
                    })
                    .catch(error => {
                        console.log("Error", error)
                    })
            }
            else {
                this.setState({ selectedDepartmentUUID: "" });
            }
        }

        const getDepaUUID = (department) => {
            var selectedDepartment = this.state.departmentData.filter(function (item) {
                return item.name === department;
            });
            return selectedDepartment[0].uuid;
        }

        const handleChangeDoctor = (event) => {
            this.setState({ selectedDoctor: event.target.value, mandatoryValidation: false })
        };

        const handleChangeDepartment = (event) => {
            this.setState({ selectedDepartment: event.target.value, mandatoryValidation: false })
            getServiceavAilability(event.target.value);

        };

        const clearHandler = (event) => {
            event.preventDefault()
            window.location.reload();
        };

        const pushAppointment = (event) => {

            var intrval = 15
            var endTime = new Date(this.selectedDate);
            if (this.state.selectedDepartmentData.durationMins != null) {
                intrval = this.state.selectedDepartmentData.durationMins
            }
            else {
                intrval = 15
            }

            if (this.state.editAppointmentUUID !== null) {
                var data = {
                    uuid: this.state.editAppointmentUUID,
                    patientUuid: this.state.patientName,
                    serviceUuid: this.state.selectedDepartmentUUID,
                    startDateTime: this.selectedDate,
                    endDateTime: endTime.setMinutes(endTime.getMinutes() + intrval),
                    providerUuid: this.state.selectedDoctor === "Select Doctor" ? null : this.state.selectedDoctor,
                    appointmentKind: "Scheduled"
                }
            }
            else {
                var data = {
                    patientUuid: this.state.patientName,
                    serviceUuid: this.state.selectedDepartmentUUID,
                    startDateTime: this.selectedDate,
                    endDateTime: endTime.setMinutes(endTime.getMinutes() + intrval),
                    providerUuid: this.state.selectedDoctor === "Select Doctor" ? null : this.state.selectedDoctor,
                    appointmentKind: "Scheduled"
                }
            }

            if (data.patientUuid != "" && data.serviceUuid != "" && this.selectedDate != "" && this.selectedDate.getHours() != 0) {
                this.setState({ mandatoryValidation: false })
                axios.post(`${url}/phrappointment`, data, { auth: this.auth })
                    .then(responce => {
                        console.log("Appointment Save")
                    })
                    .catch(error => {
                        console.log("Error", error)
                    })
                this.setState({
                    mandatoryValidation: false,
                    patientName: ""
                })
            }
            else {
                this.setState({ mandatoryValidation: true })
                event.preventDefault();
            }
        };


        const editAppHandler = (data) => {
            getServiceavAilability(data[0].service.name);
            this.setState({
                editStatus: true,
                patientName: data[0].patient.name,
                selectedDepartment: data[0].service.name,
                selectedDoctor: data[0].provider !== null ? data[0].provider.uuid : "Select Doctor",
                startTimeForEdit: new Date(data[0].startDateTime),
                editAppointmentUUID: data[0].uuid
            })

        }

        let departmentList = this.state.departmentData.length > 0
            && this.state.departmentData.map((item, i) => {
                return (
                    <option key={i} value={item.name}>{item.name}</option>
                )
            }, this);

        let doctorList = this.state.doctorData.length > 0
            && this.state.doctorData.map((item, i) => {
                if (item.attributes.length > 0) {
                    return (
                        <option key={i} value={item.uuid}>{item.person.display}</option>
                    )
                }
            }, this);

        return (
            <div>
                <div id="form">
                    <form className={classes.form} onSubmit={pushAppointment}>
                        <div className={classes.container1}>
                            <div>
                                <img src={AppointmentIcon} className={classes.iconDetails} />
                            </div>
                            <div className={classes.headerStyleAppointment}>
                                <label>Book an Appointment</label>
                            </div>
                        </div>
                        <fieldset style={{ border: 0 }}>
                            <div style={{ borderRight: "2px solid #d9d9d9", marginRight: "130px" }} >
                                <table style={{ textAlign: 'right' }} ><tbody>
                                    <tr>
                                        <td>
                                            <label style={{ marginTop: "-10%" }} className={classes.label} >Patient Name <span style={{ color: "red", fontSize: "18px" }}>*</span></label>
                                        </td>
                                        <td>
                                            <input onChange={event => this.setState({ patientName: event.target.value, mandatoryValidation: false })} value={this.state.patientName} className={classes.input} placeholder="Enter Full Name of Patient"  readOnly={this.state.editStatus} 
                                            style={
                                            this.state.editStatus ? { color: "gray",backgroundColor:"rgb(233 237 241)"} : { color: "Black" }}/>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td >
                                            <label style={{ marginTop: "-10%" }} className={classes.label}>Department <span style={{ color: "red", fontSize: "18px" }}>*</span></label>
                                        </td>
                                        <td >
                                            <select
                                                value={this.state.selectedDepartment}
                                                onChange={handleChangeDepartment}
                                                style={{ border: "none" },
                                                    this.state.selectedDepartment === "0" ? { color: "gray", fontSize: "14px" } : { color: "Black" }}
                                                className={classes.dropDown}>
                                                <option value="" selected hidden defaultValue>Select Department</option>
                                                <option value=""></option>
                                                {departmentList}
                                            </select>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td className={classes.tbltd}>
                                            <label style={{ fontSize: "14px", marginRight: "10px", marginTop: "7%" }} className={classes.label}>Doctor</label>
                                        </td>
                                        <td>
                                            <select
                                                value={this.state.selectedDoctor}
                                                onChange={handleChangeDoctor}
                                                style={{ border: "none" },
                                                    this.state.selectedDoctor === "Select Doctor" ? { color: "gray", fontSize: "14px" } : { color: "Black" }}
                                                className={classes.dropDown}>
                                                <option value="Select Doctor" selected hidden defaultValue>Select Doctor</option>
                                                <option value=""></option>
                                                {doctorList}
                                            </select>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td className={classes.tbltd}>
                                            <div>
                                                <label className={classes.label}>Date and Time <span style={{ color: "red", fontSize: "18px" }}>*</span></label>
                                            </div>
                                        </td>
                                        <td>
                                            <div style={{ borderRadius: "0px", marginLeft: "10px", marginTop: "10px", fontFamily: "OpenSans" }}>

                                                <CustomDatePicker
                                                    editStatus={this.state.editStatus}
                                                    isvalidate={this.state.mandatoryValidation}
                                                    startTimeForEdit={this.state.startTimeForEdit}
                                                    departmentData={this.state.selectedDepartmentData}
                                                    onSelectDate={handleDate} />
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            {this.state.mandatoryValidation ?
                                                <div className={classes.mandatoryValidation}>
                                                    <span><img src={WarningIcon} className={classes.WarningIcon} /> <span>Please enter all mandatory fields</span></span>
                                                </div>
                                                : null}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <div className={classes.button}>
                                                <button className={classes.buttonClear} onClick={clearHandler}>Clear</button>
                                                <button className={classes.buttonConfirm} type="submit" >Confirm</button>
                                            </div>
                                        </td>
                                    </tr></tbody>
                                </table>
                            </div>
                        </fieldset>
                    </form>

                </div>
                <div id="form">
                    <form className={classes.formNew}>
                        <div style={{ marginLeft: "15%" }}>
                            <div>
                                <img src={ScheduledIcon} alt="ScheduledIcon" className={classes.iconDetails} />
                            </div>
                            <div className={classes.headerStyleScheduled}>
                                <label>Scheduled Appointments</label>
                            </div>
                        </div>
                        <div className={classes.scheduledItem}>
                            <ScheduledAppointments onClickEditApp={editAppHandler} />
                        </div>
                    </form>
                </div>
            </div>
        );
    };
};

export default Appointment; 
