import React from 'react';
import classes from './ScheduledAppointments.module.css';
import EditIcon from '../../Images/edit.png';
import DeleteIcon from '../../Images/delete.png';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import axios from 'axios';
import moment from 'moment';

const url = process.env.REACT_APP_URL;

class ScheduledAppointmentList extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            noAppointments: "No Scheduled Appointments",
            scheduledAppointmentData: [],
            isModelVisiable: false,
            appointmentId: 0
        };
    }
    auth = {
        username: 'superman',
        password: 'Admin123'
    };

    componentDidMount() {

        let today = new Date();
        let date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + (today.getDate() - 1);
        let time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
        let dateTime = date + 'T' + time + '.000Z';


        axios.get(`${url}/phrappointment/all?forDate=${dateTime}`, {
            auth: this.auth
        })
            .then(responce => {
                console.log("Responce", responce)
                this.setState({ scheduledAppointmentData: responce.data })
            })
            .catch(error => {
                console.log("Error", error)
            })

    }
    render() {

        const editHandler = (event) => {
            var scheduledAppointment = this.state.scheduledAppointmentData.filter(function (item) {
                return item.uuid === event.target.id;
            });
            this.props.onClickEdit(scheduledAppointment);
        }

        const cancelHandler = (event) => {
            if (this.state.isModelVisiable) {
                this.setState({
                    isModelVisiable: false,
                    appointmentId: event.target.id
                })
            } else {
                this.setState({
                    isModelVisiable: true,
                    appointmentId: event.target.id
                })
            }
        }

        const closeHandler = () => {
            console.log("adfsaas")
            if (this.state.isModelVisiable) {

                this.setState({
                    isModelVisiable: false
                })
            } else {

                this.setState({
                    isModelVisiable: true
                })
            }
        }

        const cancelAppointmentHandler = (event) => {
            let data = {
                toStatus: "Cancelled"
            }
            axios.post(`${url}/appointment/${this.state.appointmentId}/changeStatus`, data, { auth: this.auth })
                .then(responce => {
                    console.log("Responffdgdgdce", responce)

                })
                .catch(error => {
                    console.log("Error", error)
                })

        }

        const LightTooltip = withStyles((theme) => ({
            tooltip: {
                backgroundColor: theme.palette.common.white,
                color: 'rgba(0, 0, 0, 0.87)',
                boxShadow: theme.shadows[2],
                fontSize: 11,
            },
        }))(Tooltip);
        let scheduledAppointmentList = this.state.scheduledAppointmentData.length > 0
            && this.state.scheduledAppointmentData.map((item, i) => {

                let theDate = moment(item.startDateTime).format("MMM DD, YYYY h:mm A");
                return (
                    <ul className={classes.ul}>
                        <li>
                            <div className={classes.main}>
                                <div className={classes.dateTimeDiv}>
                                    <label>{theDate}</label>
                                    <label className={classes.time}></label>
                                </div>
                                <div className={classes.iconDiv}>
                                    <LightTooltip title="Edit Appointment" placement="bottom-start">
                                    <img className={classes.editIcon} id={item.uuid} onClick={editHandler} src={EditIcon} alt="" />
                                    </LightTooltip>
                                    <LightTooltip title="Cancel Appointment" placement="bottom-start">
                                        <img className={classes.deleteIcon} id={item.uuid} onClick={cancelHandler} src={DeleteIcon} alt="" />
                                    </LightTooltip>
                                </div>
                                <br />
                                <label>{item.patient.name}</label>
                                <br />
                                <label>{item.service.name}</label>
                                <br />
                                {item.provider !== null ? <label>{item.provider.name}</label> : null}
                            </div>

                            <div id="myModal" className={classes.modal} style={this.state.isModelVisiable ? { display: "block" } : { display: "none" }}>

                                <div className={classes.modalContent}>
                                    <div className={classes.close} >
                                        <span style={{ paddingRight: "50px" }} onClick={closeHandler} style={this.state.isModelVisiable ? { display: "block" } : { display: "none" }}>&times;</span>
                                    </div>
                                    <div className={classes.modalBody}>
                                        <label  >Are you sure you want to cancel your appointment ?</label>
                                        <div style={{ marginTop: "10px" }}>
                                            <button className={classes.buttonYes} onClick={cancelAppointmentHandler} >Yes</button>
                                            <button className={classes.buttonNo} >No</button>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                )
            }, this);

        return (
            <div >
                {this.state.scheduledAppointmentData.length > 0 ? scheduledAppointmentList :
                    <div className={classes.noScheduledContent}>
                        {this.state.noAppointments}
                    </div>}

            </div>
        );
    };
};
export default ScheduledAppointmentList;